<template>
  <div class="container" style="max-width: 100%;">
    <b-modal id="agendar" title="Agendar Horário">
      <FormTodo v-on:add-todo="addComment"></FormTodo>
    </b-modal>
    <div class="list-group" style="max-width: 100%;">
      <b-container class="bv-example-row mb-2">
        <b-row cols="6">
          <b-col md="2" class="list-group-item">
            <p>Nome</p>
          </b-col>
          <b-col md="2" class="list-group-item">
            <p>Data</p>
          </b-col>
          <b-col md="2" class="list-group-item">
            <p>Das</p>
          </b-col>
          <b-col md="2" class="list-group-item">
            <p>às</p>
          </b-col>
          <b-col md="2" class="list-group-item">
            <p>Doutor(a)</p>
          </b-col>
        </b-row>
      </b-container>
      <p>AAAAAAAAAAAAAAAAAAh</p>
      <div v-for="(comment, index) in allComments" v-bind:key="comment">
        <b-container class="bv-example-row mb-2">
          <b-row cols="6">
            <b-col md="2" class="list-group-item">
              <p class="comment_author">
                <strong>{{ comment.name }}</strong>
              </p>
            </b-col>
            <b-col md="2" class="list-group-item">
              <p>
                <strong>{{ comment.date }}</strong>
              </p>
            </b-col>
            <b-col md="2" class="list-group-item">
              <p>
                <strong>{{ comment.timeEnt }}</strong>
              </p>
            </b-col>
            <b-col md="2" class="list-group-item">
              <p>
                <strong>{{ comment.timeExit }}</strong>
              </p>
            </b-col>
            <b-col md="2" class="list-group-item">
              <p>
                <strong>{{ comment.selected }}</strong>
              </p>
            </b-col>
            <b-col md="2" class="list-group-item">
              <a href="#" title="Excluir" v-on:click.prevent="removeComment(index)">Excluir</a>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
  </div>
</template>

<script>
import FormTodo from "./FormTodo";
export default {
  components: {
    FormTodo
  },
  data() {
    return {
      comments: []
    };
  },

  methods: {
    addComment(comment) {
      this.comments.push(comment);
    },

    removeComment(index) {
      this.comments.splice(index, 1);
    }
  },

  computed: {
    allComments() {
      return this.comments.map(comment => ({
        ...comment
      }));
    }
  },

  watch: {
    comments(val) {
      console.log("val", val);
    }
  }
};
</script>
